<template>
  <van-nav-bar
    title="极速看片"
    fixed="true"
    class="title-class"
  />
  <van-image
  width="100%"
  height="50"
  src="https://img01.yzcdn.cn/vant/apple-1.jpg"
  class="top-banner"
/>
  <van-list
    v-model="loading"
    :finished="finished"
    finished-text="-------我们也是有底线的-------"
    @load="onLoad"
    class="goods"
  >
    <div v-for="(item, index) in pageList" :key="item.id">
      <van-row
        align="center"
        gutter="20"
        v-if="index % 2 == 0"
        class="row"
      >
        <van-col span="12"  @click="onClickVedio(index)">
          <van-image
            fit="fill"
            :src="pageList[index].cover"
            :height="imageHeight"
            class="item"
          >
            <span v-if="true" class="sidai"><div style="color: #fff">NEW</div></span>
            <span class="duration"
              ><div style="color: #fff">01:00:00</div></span
            
            >
          </van-image>
          <div class="van-ellipsis video-title">
            {{ pageList[index].title }}
          </div>
       
        </van-col>
        <van-col
          span="12"
          @click="onClickVedio(index + 1)"
          v-if="index + 1 < pageList.length"
        >
          <van-image
            fit="fill"
            :height="imageHeight"
            class="item"
            :src="pageList[index + 1].cover"
          >
            <span v-if="false" class="sidai"><div style="color: #fff">NEW</div></span>
            <span class="duration"
              ><div style="color: #fff">03:00:00</div></span
            >
          </van-image>
          <div class="van-ellipsis video-title">
            {{ pageList[index + 1].title }}
          </div>
        </van-col>
      </van-row>
    </div>
      <van-loading v-if="this.finished == false" id="loaiding" type="spinner" class="loading"/>
  </van-list>
   <van-image
  width="100%"
  height="50"
  src="https://img01.yzcdn.cn/vant/apple-1.jpg"
  class="buttom-banner"
/>
</template>

<script>
import config from "../config";
import {
  NavBar,
  Image,
  Col,
  Row,
  List,
  Loading
} from "vant";

export default {
  components: {
    [NavBar.name]: NavBar,
    [Image.name]: Image,
    [List.name]: List,
    [Col.name]: Col,
    [Row.name]: Row,
    [Loading.name]:Loading
  },

  mounted() {
    this.imageHeight = (window.screen.width / 2 - 10) /9 * 16 *0.9;
  },

  data() {
    return {
      active: "电影",
      currentTag: "电影",
      currentPage: 1,
      list: [],
      pageList: [],
      loading: false,
      finished: false,
      imageHeight:0
    };
  },

  computed: {
  },

  methods: {
    onClickVedio(index) {
      this.$router.push({
        path: "/sub/",
        query: { id: this.pageList[index].slug },
      });
    },

    getData() {
      this.$http.get(config.api).then((response) => {
        // this.info = response;
        this.list = response.data;
        console.log(response);
        for (let i = 0; i < this.list.length; i++) {
          this.pageList.push(this.list[i]);
        }
      });
    },

    onLoad() {
      if (this.loading) {
        return;
      }
      this.loading = true;
      this.$http.get(config.api).then((response) => {
        this.loading = false;
        // 加载状态结束

        // 数据全部加载完成
        if (this.pageList.length >= 40) {
          this.finished = true;
        } else {
          this.list = response.data;
          console.log(this.list);
          for (let i = 0; i < this.list.length; i++) {
            this.pageList.push(this.list[i]);
          }
        }
      });
    },
  },
};
</script>

